.container { max-width: 900px; margin: 40px auto; background: #232527; border-radius: 12px; padding: 24px; color: #fff; }
.title { font-size: 22px; margin-bottom: 12px; }
.empty { color: #bbb; }
.table { width: 100%; border-collapse: collapse; }
.table th, .table td { padding: 8px 12px; border-bottom: 1px solid rgba(255,255,255,0.06); }
.smallButton { padding:8px 12px; border-radius:6px; cursor:pointer; background:#111; color:#fff; border:1px solid rgba(255,255,255,0.04) }
